}

.plain-selector {
  margin: 0 3px;
}

.plain-selector::after {
  content: "";
  position: absolute;
  z-index: 2;
  bottom: 10%;
  margin-top: -3px;
  height: 0;
  width: 0;
  right: -5px;
  border-top: 6px solid black;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  pointer-events: none;
}

.plain-selector::before {
  content: "";
  position: absolute;
  z-index: 2;
  top: 20%;
  margin-top: -3px;
  height: 0;
  width: 0;
  right: -5px;
  border-bottom: 6px solid black;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  pointer-events: none;
}

.out-selector {
  position: relative;
}

.outer-selection {
  width: 100%;
}

.selection {
  display: block;
  margin: auto;
  border-radius: 50px;
  background: white;
