  .awesomplete > ul:empty {
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
    display: block;
    -webkit-transition-timing-function: ease;
            transition-timing-function: ease;
  }
}

/* Pointer */
.awesomplete > ul:before {
  content: "";
  position: absolute;
  top: -.43em;
  left: 1em;
  width: 0;
  height: 0;
  padding: .4em;
  background: white;
  border: inherit;
  border-right: 0;
  border-bottom: 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

.awesomplete > ul > li {
  position: relative;
  padding: .2em .5em;
  cursor: pointer;
  color: #555;
  font-size: 15px;
  width: 300px;
}

.awesomplete > ul > li:hover {
  background: #b8d3e0;
  color: black;
}

.awesomplete > ul > li[aria-selected="true"] {
  background: #3d6d8f;
  color: white;
}

.awesomplete mark {
  background: #eaff00;
}

.awesomplete li:hover mark {
  background: #b5d100;
}

.awesomplete li[aria-selected="true"] mark {
  background: #3d6b00;
  color: inherit;
}

