/* CSS browser reset */
* {
  margin: 0;
  padding: 0;
}

html {
  visibility: hidden;
  opacity: 0;
}

/* OVERALL */
body {
  font-family: Helvetica, Arial, sans-serif, sans-serif;
  margin: 0;
  -webkit-backface-visibility: hidden;
}

/* BOARDED STATUS */

.boarded-wrapper {
  width: 100%;
  line-height: 40px;
  font-size: 20px;
  font-weight: bold;
  z-index: 10;
  position: relative;
}

.animate-boarded {
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
  animation-name: colorPulse;
}

@keyframes colorPulse {
  0% {
    background-color: #4b0303;
  }
  50% {
    background-color: #e40101;
  }
  100% {
    background-color: #4b0303;
  }
}

.boarded-arrived {
  -webkit-animation-play-state: paused !important;
  -moz-animation-play-state: paused !important;
  -o-animation-play-state: paused !important;
  animation-play-state: paused !important;
  background-color: #12d463;
}

.boarded-status {
  width: 1000px;
  margin: 0 auto;
  height: 50px;
  line-height: 50px;
  font-size: 20px;
  font-weight: bold;
  color: #eee;
}

.boarding-close-icon {
  float: right;
  margin-top: 7px;
}

.fixedElement {
  background-color: #c0c0c0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 100;
}

/* HEADER */

header {
  background-color: #444;
  font-weight: 600;
  color: #eee;
  width: 100%;
  padding: 20px 0;
  text-transform: uppercase;
  position: relative;
  /* top: 50px; */
  display: inline-block;
  max-height: 20px;
  box-shadow: 1px 1px 2px 0 #666;
}

.overlay-wrapper {
  width: 100%;
}

.overlay {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: #ddd;
  z-index: -1;
  filter: blur(8px);
  -webkit-filter: blur(8px);
  -moz-filter: blur(8px);
  -o-filter: blur(8px);
  -ms-filter: blur(8px);
}

header .header-wrapper {
  width: 1000px;
  margin: 0 auto;
}

.settings span {
  cursor: pointer;
}
.settings span select {
  cursor: pointer;
}
.settings input, .settings label {
  cursor: pointer;
}

.sf-menu.settings {
  float: right;
  position: relative;
  top: -26px;
}
.sf-menu.settings li {
  background-color: #444;
  font-size: 15px;
}

.fixed {
  position: fixed;
}

img.loading-gif.fixed {
  /* background-color: white; */
  /* padding: 20px; */
  /* margin: 0 auto; */
  top: 200px;

  z-index: 100;
  left: 45%;
}

/* AM PM TOGGLE */
/*  NOTE: from: https://codepen.io/nickbottomley/pen/uhfmn  */

/* main styles */
.switch {
  margin-left: 175px;
  position: absolute;
  top: 8px;
}
.switch input {
  position: absolute;
  top: 0;
  z-index: 2;
  opacity: 0;
  cursor: pointer;
}
.switch input:checked {
  z-index: 1;
}
.switch input:checked + label {
  opacity: 1;
  cursor: default;
}
.switch input:not(:checked) + label:hover {
  opacity: 0.5;
}
.switch label {
  color: #fff;
  opacity: 0.33;
  transition: opacity 0.25s ease;
  cursor: pointer;
}
.switch .toggle-outside {
  height: 100%;
  border-radius: 2rem;
  padding: 0.25rem;
  overflow: hidden;
  transition: 0.25s ease all;
}
.switch .toggle-inside {
  border-radius: 5rem;
  background: #4a4a4a;
  position: absolute;
  transition: 0.25s ease all;
}
.switch-horizontal input {
  height: 3rem;
  width: 6rem;
  left: 6rem;
  margin: 0;
}
.switch-horizontal label {
  font-size: 20px;
  line-height: 3rem;
  display: inline-block;
  width: 6rem;
  height: 100%;
  margin: 0;
  text-align: center;
}
.switch-horizontal label:last-of-type {
  margin-left: 60px;
}
.switch-horizontal .toggle-outside {
  background: #fff;
  height: 32px;
  position: absolute;
  width: 80px;
  left: 6rem;
}
.switch-horizontal .toggle-inside {
  height: 2rem;
  width: 2rem;
}
.switch-horizontal input:checked ~ .toggle-outside .toggle-inside {
  left: 0.25rem;
}
.switch-horizontal input ~ input:checked ~ .toggle-outside .toggle-inside {
  left: 3.25rem;
}


/* APPLE STYLE SWITCH */

input[type="checkbox"].ios8-switch {
  position: absolute;
  margin: 8px 0 0 16px;
}
input[type="checkbox"].ios8-switch + label {
  position: relative;
  padding: 5px 0 0 50px;
  line-height: 2.0em;
}
input[type="checkbox"].ios8-switch + label:before {
  content: "";
  position: absolute;
  display: block;
  left: 0;
  top: 0;
  width: 40px; /* x*5 */
  height: 24px; /* x*3 */
  border-radius: 16px; /* x*2 */
  background: #fff;
  border: 1px solid #d9d9d9;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
input[type="checkbox"].ios8-switch + label:after {
  content: "";
  position: absolute;
  display: block;
  left: 0px;
  top: 0px;
  width: 24px; /* x*3 */
  height: 24px; /* x*3 */
  border-radius: 16px; /* x*2 */
  background: #fff;
  border: 1px solid #d9d9d9;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
input[type="checkbox"].ios8-switch + label:hover:after {
  box-shadow: 0 0 5px rgba(0,0,0,0.3);

}
input[type="checkbox"].ios8-switch:checked + label:after {
  margin-left: 16px;
}
input[type="checkbox"].ios8-switch:checked + label:before {
  background: #55D069;
}
/* END APPLE STYLE SWITCH */

/* BEGIN APPLE STYLE DROPDOWN SELECT  */
select.settings.arrival-time-pad.ios-style-dropdown, select.settings.alert-threshold.ios-style-dropdown  {
  top: 2px;
  left: -9px;
  font-weight: bold;
}
.ios-style-dropdown{
  font-size: 20px;
  background: white;
  border:none;
  position: relative;
  -webkit-appearance: none;
  padding-right: 10px;
}
.ios-style-dropdown:focus{
  outline: none;
}
.plain-selector{
  margin: 0 3px;
}
.plain-selector::after{
  content: "";
  position: absolute;
  z-index: 2;
  bottom: 10%;
  margin-top: -3px;
  height: 0;
  width: 0;
  right: -5px;
  border-top: 6px solid black;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  pointer-events: none;
}
.plain-selector::before{
  content: "";
  position: absolute;
  z-index: 2;
  top: 20%;
  margin-top: -3px;
  height: 0;
  width: 0;
  right: -5px;
  border-bottom: 6px solid black;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  pointer-events: none;
}
.out-selector{
  position: relative;
}
.outer-selection{
  width: 100%;
}
.selection{
  display: block;
 	margin: auto;
 	border-radius: 50px;
  background: white;
  color: #333;
  font-size: 13px;
 	max-width: 360px;
 	text-align: center;
 	width: 100%;
 	position: relative;
 }

 /* END APPLE STYLE DROPDOWN SELECT */

