/* MEDIA QUERIES FOR MOBILE */

/*  iPhone X  */
@media only screen
  and (device-width: 375px)
  and (device-height: 812px)
  and (-webkit-device-pixel-ratio: 3) {
    body {
      -webkit-appearance: none;
      /* padding: 50px; */
    }
  }

/*  iPhone 7/8  */
@media only screen
  and (device-width: 375px)
  and (device-height: 667px)
  and (-webkit-device-pixel-ratio: 2) {
    body {
      -webkit-appearance: none;
      /* padding: 50px; */
    }
   }

/*  iPhone 6+/6s+/7+/8+  */
@media only screen
  and (device-width: 414px)
  and (device-height: 736px)
  /* and (orientation: portrait) */
  /* USER AGENT STRING: 'Mozilla/5.0 (iPhone; CPU OS 11_0 like Mac OS X) AppleWebKit/604.1.25 (KHTML, like Gecko) Version/11.0 Mobile/15A372 Safari/604.1
' */
  and (-webkit-device-pixel-ratio: 3) {
    body {
      -webkit-appearance: none;
      /* padding: 50px; */
    }

    header .header-wrapper {
      width: 900px;
    }

    .overlay {
      -webkit-filter: blur(8px);
      -moz-filter: blur(8px);
      -o-filter: blur(8px);
      -ms-filter: blur(8px);
      filter: blur(8px);
      opacity: 0.7;
    }

    .trainData-am, .trainData-pm {
      width: 900px;
    }

    span.brand-callout {
      font-size: 26px;
    }

    #configure-commute h2 {
      font-size: 30px;
      margin-bottom: 40px;
    }

    .system-alerts h2 {
      /* margin: 20px 0; */
      font-size: 30px;
    }

    h2.am-commute-station-label, h2.am-commute-station-label {
      font-size: 30px;
    }

    .content-wrapper {
      width: 900px;
    }

    .boarded-status {
      width: 900px;
    }

    .sf-menu.settings {
      top: -38px;
    }

    h2 {
      font-size: 1em;
    }
    .alertbox b {
      font-size: 16px;
    }

    .alertbox p {
      font-size: 12px;
    }

    .awesomplete {
      bottom: 3px;
    }

    .awesomplete > ul > li {
      font-size: 30px;
      width: 600px;
    }

    .footer-callout {
      font-size: 6px;
      margin-bottom: 20px;
    }

    .switch {
      margin-left: 0;
      top: 10px;
    }

    span.sf-with-ul {
      top: 20px;
    }

    li.arrival-time {
      font-size: 2.2em;
      padding: 40px;
    }

    li.arrival-time canvas {
      height: 50px;
      width: 50px;
    }

    li.route-data {
      font-size: 1.5em;
    }

  }

@media only screen
  and (device-width: 414px)
  and (device-height: 736px)
  /* and (orientation: landscape) */
  and (-webkit-device-pixel-ratio = 3) {
    body {
      padding: 50px;
    }
  }
